.promo {
    min-height: 650px;
    padding: 21px 0 93px 0;
    background: url(../img/bg/first_bg.png) center (center/cover) no-repeat;
    &__wrapper {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        margin-top: 90px; }
    &__header {
        color: rgb(255, 255, 255);
        font-family: 'MuseoSansCyrl-700';
        font-size: 42px;
        font-weight: 700;
        line-height: 50px;
        text-transform: uppercase; }
    &__subheader {
        color: rgb(255, 255, 255);
        font-family: 'MuseoSansCyrl-500';
        font-size: 24px;
        font-weight: 500;
        line-height: 30px;
        text-transform: uppercase; }
    &__icons {
        margin-top: 60px;
        width: 425px;
        display: flex;
        justify-content: space-between; }
    &__icon {
        width: 108px;
        text-align: center;
        color: rgb(255, 255, 255);
        font-family: 'MuseoSansCyrl-500';
        font-size: 14px;
        font-weight: 500;
        line-height: 14px;
        img {
            display: block;
            margin: 0 auto;
            margin-bottom: 20px; } }
    &__link {
        color: rgb(255, 255, 255);
        font-family: 'MuseoSansCyrl-500';
        font-size: 16px;
        font-weight: 500;
        line-height: 25px;
        margin-top: 10px;
        a {
            color: rgb(255, 255, 255); } } }
